<template>
    <div>
        <Row> <!-- v-permission.show="'m:product:add'" -->
            <Button type="primary" @click="add" >
            <Icon type="plus"></Icon>新增子产品</Button>
            <Button class="back" type="primary" @click.prevent="$router.go(-1)">
                <Icon type="md-arrow-back"></Icon>
                返回
            </Button>
        </Row>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">子产品名称:</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 130px" />
            <label class="margin-left-10" style="margin-right:8px;">子产品价格:</label>
            <Input v-model="table.query.price" placeholder="" clearable style="width: 130px" />
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float:right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <div style="height: 400px">
                <Form ref="form" :model="vo" :label-width="120" :rules="validate">
                    <Form-item label="子产品名称" prop="name">
                        <Input v-model.trim="vo.name" :maxlength="50" />
                    </Form-item>
                    <Form-item label="子产品价格(分)" prop="price">
                        <Input v-model.trim="vo.price" :maxlength="10" />
                    </Form-item>
                    <Form-item label="积分抵扣额度" prop="rebate">
                        <Input v-model.trim="vo.rebate" :maxlength="3" />
                    </Form-item>
                </Form>
            </div>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
export default {
    name: 'SubProductList',
    mixins: [ TableColRender, TableDataView, ModelView ],
    data () {
        return {
            product: {
                id: this.$route.query.id,
                name: this.$route.query.name
            },
            table: {
                col: [{
                    title: '产品编号',
                    key: 'id'
                }, {
                    title: '产品名称',
                    key: 'name'
                }, {
                    title: '产品价格（分）',
                    key: 'price'
                }, {
                    title: '积分抵扣额度',
                    key: 'rebate'
                }, {
                    title: '父产品名称',
                    key: 'prodName'
                }, {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }, /* {
                    title: '更新时间',
                    key: 'updateTime',
                    render: this.renderDate
                }, */ {
                    title: '操作',
                    key: 'action',
                    width: 400,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' } },
                        click: () => this.update(params.row),
                        text: '修改'
                    }, {
                        type: 'delete',
                        click: () => this.remove(params.row)
                    }])
                }],
                url: '/api/nmedia/sub/product/',
                query: {
                    name: null,
                    price: null,
                    prodId: this.$route.query.id
                }
            },
            vo: {
                prodId: this.$route.query.id,
                price: null,
                name: null,
                prodName: null,
                id: null,
                rebate: null,
                createTime: new Date()
            },
            validate: {
                price: [{ required: true, trigger: 'blur', pattern: /^[0-9]*[1-9][0-9]*$/, message: '产品价格必须为正整数' }],
                name: [{ required: true, trigger: 'blur', message: '产品名称不能为空' }],
                rebate: [{ required: true, trigger: 'blur', pattern: /(^[1-9][0-9]$|^[0-9]$|^100$)/, message: '积分抵扣额度必须为0-100的整数' }]
            },
            addUrl: 'api/nmedia/sub/product/',
            updateUrl: 'api/nmedia/sub/product/update',
            removeUrl: 'api/nmedia/sub/product'
        };
    },
    computed: {
        isUpdate () {
            return this.vo.id !== null && this.vo.id !== undefined;
        },
        generateAddOrUpdate () {
            return { ...this.vo };
        }
    },
    methods: {
        async add () {
            this.$super(ModelView).add();
            this.vo.id = null;
        },
        async update (item) {
            this.loadingBtn = true;
            Object.keys(this.vo).forEach(key => this.vo[key] = item[key]);
            this.model = true;
        }
    }
};
</script>

<style scoped>
.title {
    display: inline-block;
}
.back {
    float: right;
}
</style>
